.userlist-box {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-preferred-size: 20%;
	    flex-basis: 20%;

	padding: 20px;
}

.light .userlist-box {
	background-color: #dddddd;
}

.dark .userlist-box {
	background-color: #393939;
}

.offline {
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
}

.dark .online {
	color: white;
}

.dark .offline {
	color: #aaaaaa;
}

.light .offline {
	color: #777777;
}

.light .online {
	color: #222222;
}

.userlist-item {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	height: 3em;
}

.image {
	border-radius: 100%;
	width: 40px;
	height: 40px;
	display: inline-block;
	background-size: cover;
	background-position: 50% 50%;
}

.name {
	display: inline-block;
	padding-left: 10px;
}

.userlist-label {
	margin: 5px 0;
	font-size: 1.2em;
}

.dark .userlist-label {
	border-bottom: 1px solid white;
}

.light .userlist-label {
	border-bottom: 1px solid black;
}


