.channellist-box {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-preferred-size: 20%;
	    flex-basis: 20%;

	padding-top: 10px;
}

.dark .channellist-box {
	background-color: #393939;
}

.light .channellist-box {
	background-color: #dddddd;
}

.channellist-item {
	padding: 6px;
	padding-left: 10px;
	margin: 5px 10px;
	border-radius: 10px;
}

.channellist-item.selected {
	padding: 10px;
	font-weight: bold;
}

.dark .channellist-item {
	color: #888888;
}

.dark .channellist-item.selected {
	background-color: #555555;
	color: #aaaaaa;
}

.light .channellist-item {
	color: #555555;
}

.light .channellist-item.selected {
	color: #000000;
	background-color: #cccccc;
}


