.chatapp {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: stretch;
	    align-items: stretch;
	height: 100%;
}

.dark {
	background-color: #444444;
	color: white;
}

.light {
	background-color: white;
	color: black;
}

.divider {
	margin: 5px 50px;
	border-bottom-width: 1px;
	border-bottom-style: solid;
}

.dark .divider {
	border-bottom-color: #555555;
}

.light .divider {
	border-bottom-color: #dddddd;
}


