#theme-changer-box {
	position: fixed;
	left: 0;
	bottom: 0;
	padding: 15px;
	border-top-right-radius: 10px;
}

.dark #theme-changer-box {
	color: black;
	background-color: white;
}

.light #theme-changer-box {
	color: white;
	background-color: #444444;
}

#theme-changer-box label {
	padding-left: 5px;
	cursor: pointer;
}

#theme-changer-box input {
	cursor: pointer;
}


